ding: 12px;\n  display: none;\n  align-items: center;\n  justify-content: center;\n  background-color: #e2bfb3;\n  color: white;\n  flex: auto;\n}\n\n#navigationDrawer {\n  width: 250px;\n  height: 100%;\n  position: absolute;\n\n  transform: translate(-250px, 0);\n  transition: transform 0.3s ease-in-out;\n  background-color: #feece2;\n}\n\n#navigationDrawer ul li {\n  padding: 12px;\n}\n\n#navigationDrawer ul li a {\n  color: #e2bfb3;\n  text-decoration: none;\n  padding: 12px;\n}\n\n#navigationDrawer.open {\n  transform: translate(0, 0)\n}\n\n#hamburgerButton {\n  display: none;\n  padding: 12px;\n  align-items: center;\n  justify-content: center;\n  background-color: #e2bfb3;\n  color: white;\n  flex: auto;\n}\n\n.jumbotron {\n  flex: 1;\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  flex: 1 1;\n}\n\n.jumbotron img {\n  width: 100%;\n  height: 500px;\n  object-fit: cover;\n  border: 1px solid #e2bfb3;\n}\n\n.daftarrestoran {\n  padding: 16px;\n  border-radius: 10px;\n}\n\n.daftarrestoran>h2 {\n  text-align: center;\n  color: #e2bfb3;\n}\n\n.restaurant {\n  padding: 8px 16px 16px 16px;\n  border: 1px solid black;\n  border-radius: 5px;\n  margin: 10px 10px;\n  max-width: 270px;\n}\n\n.restaurant img {\n  object-fit: cover;\n  height: 50%;\n  width: 100%;\n}\n\nfigure img {\n  object-fit: cover;\n  height: 100%;\n  width: 100%;\n}\n\n.restaurant p {\n  text-align: justify;\n  padding-bottom: 20px;\n}\n\n.restaurant_list {\n  padding: 16px;\n  display: flex;\n  flex-direction: row;\n  flex-wrap: wrap;\n  justify-content: center;\n}\n\n.foodAndBeverageContainer {\n  display: flex;\n  border-style: solid;\n  margin: 20px 20px 20px 20px;\n}\n\n.foodContainer {\n  flex: 1 1 50%;\n  border-style: solid;\n  text-align: center;\n}\n\n.foodTitle {\n  border-style: hidden hidden solid hidden;\n}\n\nfooter {\n  padding: 12px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background-color: #e2bfb3;\n  color: white;\n  flex: 